import { Component, OnInit, Input, OnDestroy } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { onBackButtonClick } from 'src/app/utils/parallax-header';
import { EventsService } from 'src/app/services/events.service';

@Component({
  selector: 'app-ingredient',
  templateUrl: './ingredient.page.html',
  styleUrls: ['./ingredient.page.scss'],
})
export class IngredientPage implements OnInit {
  ingredient: any = {
    name: "",
    description: "",
    kcals: 0,
    carbs: 0,
    proteins: 0,
    fats: 0,
    categories: []
  }

  constructor(
    public route: ActivatedRoute, public events: EventsService) {
    this.route.queryParams.subscribe((params: any) => {
      const ingredient: any = JSON.parse(params.ingredient)
      const keys = ['name', 'id', 'description', 'kcals', 'carbs', 'proteins', 'fats', 'thumbnail'];
      for (let key of keys) {
        this.ingredient[key] = ingredient[key];
      }
    });
  }

  ngOnInit() {
    this.events.subscribe('ON_BACK_BUTTON_CLICKED', onBackButtonClick);
  }

  ngOnDestroy() {
    this.events.destroy('ON_BACK_BUTTON_CLICKED');
  }

  ionViewDidEnter() {
    this.events.publish('ON_PAGE_DID_ENTER');
  }

  backButtonClick($event) {
    onBackButtonClick();
  }
}
